import { Component, OnInit } from '@angular/core';
import {ApiService} from '../../services/api.service'
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-userchat',
  templateUrl: './userchat.component.html',
  styleUrls: ['./userchat.component.css']
})
export class UserchatComponent implements OnInit {
private chats :chat[];
private no :number;
  constructor(private apiservice :ApiService,private route: ActivatedRoute) { }

  ngOnInit() {

    this.route.params.subscribe(params => {
      this.no = +params['mobile_number']; // (+) converts string 'id' to a number
      console.log("hello"+this.no);
      // In a real app: dispatch action to load the details here.
   });
     this.userchat(this.no);
  }
  userchat(mob:any){
this.apiservice.user_chat(mob).subscribe(data=>{
  this.chats = data.data;
   console.log(this.chats);
});
}

}
interface chat {
   id:String;
   name:String;
   email_id:String;
   mobile_number:Number;
   request_message:String;
   response_message:String;
}
