import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule }    from '@angular/http';
import { FormsModule } from '@angular/forms';
import { RouterModule,Routes} from'@angular/router';

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { ChatComponent } from './components/chat/chat.component';

import {ApiService} from './services/api.service';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { LeftsideComponent } from './components/leftside/leftside.component';
import { PeopleComponent } from './components/people/people.component';
import { NewUserComponent } from './components/new-user/new-user.component';
import { RecentUserComponent } from './components/recent-user/recent-user.component';
import { UserchatComponent } from './components/userchat/userchat.component';
import { RecentchatComponent } from './components/recentchat/recentchat.component'

const appRoutes:Routes = [
{path:'',component:PeopleComponent},
{path:'chat',component:ChatComponent},
{path:'dashboard',component:DashboardComponent},
{path:'people/newuser',component:NewUserComponent},
{path:'people/recentuser',component:RecentUserComponent},
{path:'userchat/:mobile_number',component:UserchatComponent },
{path:'recentchat/:mobile_number',component:RecentchatComponent}
]




@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    ChatComponent,
    DashboardComponent,
    LeftsideComponent,
    PeopleComponent,
    NewUserComponent,
    RecentUserComponent,
    UserchatComponent,
    RecentchatComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FormsModule
  ],
  providers: [ApiService],
  bootstrap: [AppComponent]
})
export class AppModule { }
