import { Component, OnInit } from '@angular/core';
import {ApiService} from '../../services/api.service'
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-recentchat',
  templateUrl: './recentchat.component.html',
  styleUrls: ['./recentchat.component.css']
})
export class RecentchatComponent implements OnInit {
private chats:chat[];
  constructor(private apiservice :ApiService,private route:ActivatedRoute) { }
private no :any;
  ngOnInit() {
    this.route.params.subscribe(params => {
      this.no = +params['mobile_number']; // (+) converts string 'id' to a number
      console.log("hello"+this.no);
      // In a real app: dispatch action to load the details here.
   });
    this.recentchat(this.no);
  }
  recentchat(mob:any){
this.apiservice.recent_chat(mob).subscribe(data=>{
  this.chats = data.data;
   console.log(this.chats);
});
}
}
interface chat {
   id:String;
   name:String;
   email_id:String;
   mobile_number:Number;
   request_message:String;
   response_message:String;
}
