import { Component, OnInit } from '@angular/core';
import {ApiService} from '../../services/api.service';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-recent-user',
  templateUrl: './recent-user.component.html',
  styleUrls: ['./recent-user.component.css']
})
export class RecentUserComponent implements OnInit {
private recentUser:user[];
  constructor(private apiservice :ApiService) { }

  ngOnInit() {
      this.getrecentUser()
  }
  
  getrecentUser(){
this.apiservice.recent_users().subscribe(data=>{
  this.recentUser = data.data;
   console.log(this.recentUser);
  });
 }
   
}

interface user {
   id:String;
   name:String;
   mobile_number:String;
   created_datetime:String;
   email_id:String;
}
